import { getCookie, setCookie } from '../../utils/cookies';
import { addQueryArgs } from '../../utils/url';

export const woocommerce = { setAlternativeInSession };

function setAlternativeInSession( ajaxurl: string ): void {
	const alternative = Number.parseInt( getCookie( 'nabAlternative' ) ?? '' );
	if ( isNaN( alternative ) ) {
		return;
	} //end if

	const wooAlternativeSynched = Number.parseInt(
		getCookie( 'nabWCAlternative' ) ?? ''
	);
	if ( alternative === wooAlternativeSynched ) {
		return;
	} //end if

	const image = document.createElement( 'img' );

	const url = addQueryArgs( ajaxurl, {
		action: 'nab_woocommerce_sync_alternative',
		alternative,
	} );

	image.setAttribute( 'src', url );

	setCookie( 'nabWCAlternative', alternative );
} //end setAlternativeInSession()
